import { hbs } from 'ember-cli-htmlbars';
export default hbs('{{#if this.fw.isBs4}}\n  <TopNavBs4 />\n  <div class={{this.containerClass}}>\n    <h2>Install</h2>\n    <pre>ember install ember-models-table@4</pre>\n    {{outlet}}\n  </div>\n{{/if}}\n{{#if this.fw.isBs5}}\n  <TopNavBs5 />\n  <div class={{this.containerClass}}>\n    <h2>Install</h2>\n    <pre>ember install ember-models-table@4</pre>\n    {{outlet}}\n  </div>\n{{/if}}\n{{#if this.fw.isPlainHtml}}\n  <TopNavPlainHtml />\n  <div class={{this.containerClass}}>\n    <h2>Install</h2>\n    <pre>ember install ember-models-table@4</pre>\n    <p class=\"alert\">\n      <strong>IMPORTANT!</strong> Styles for plain-html theme are <strong>NOT</strong> included to the package. You should use your own or copy from <a href=\"https://github.com/onechiporenko/ember-models-table/blob/master/tests/dummy/app/styles/plain-html.scss\" target=\"_blank\" rel=\"noopener noreferrer\">dummy/plain-html.scss</a>.\n    </p>\n    {{outlet}}\n  </div>\n{{/if}}\n{{#if this.fw.isPaper}}\n  <TopNavPaper />\n  <this.PaperContent scroll-y=\"true\" class=\"layout-padding flex\">\n    <h2>Install</h2>\n    <pre>ember install ember-models-table@4</pre>\n    <div id=\"main\" class=\"flex layout-column\" tabindex=\"-1\" role=\"main\">\n      <this.PaperToolbar @accent={{true}} class=\"md-whiteframe-1dp\" as |toolbar|>\n        <toolbar.tools>\n          <span class=\"flex\"><strong>IMPORTANT!</strong> Styles for ember-paper theme are <strong>NOT</strong> included to the package. You should use your own or copy it from <a href=\"https://github.com/onechiporenko/ember-models-table/blob/master/tests/dummy/app/styles/paper.scss\" target=\"_blank\" rel=\"noopener noreferrer\">dummy/paper.scss</a>.</span>\n        </toolbar.tools>\n      </this.PaperToolbar>\n      {{outlet}}\n    </div>\n  </this.PaperContent>\n{{/if}}\n{{#if this.fw.isDefault}}\n  <h2>Install</h2>\n  <pre>ember install ember-models-table@4</pre>\n  {{outlet}}\n{{/if}}', { moduleName: 'dummy/templates/application.hbs' });
