import { hbs } from 'ember-cli-htmlbars';
export default hbs('<this.PaperTabs @selected={{this.currentRouteName}} @borderBottom={{true}} @onChange={{this.noop}} as |tabs|>\n  {{#each @routes as |route|}}\n    <tabs.tab @value={{route.route}} @href={{emt-href-to route.route}}>\n      {{#if route.icon}}\n        <i class={{route.icon}}></i>\n      {{/if}}\n      {{route.name}}\n    </tabs.tab>\n  {{/each}}\n</this.PaperTabs>\n\n{{yield}}', { moduleName: 'dummy/templates/components/examples-nav-paper.hbs' });
