import { hbs } from 'ember-cli-htmlbars';
export default hbs('<p>\n  <PaperCheckbox\n    @value={{@showComponentFooter}}\n    @onChange={{fn (mut @showComponentFooter)}}>\n    \"Show Footer (<code>showComponentFooter</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@showColumnsDropdown}}\n    @onChange={{fn (mut @showColumnsDropdown)}}>\n    \"Show Columns Dropdown (<code>showColumnsDropdown</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@useFilteringByColumns}}\n    @onChange={{fn (mut @useFilteringByColumns)}}>\n    \"Use filtering by columns (<code>useFilteringByColumns</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@showGlobalFilter}}\n    @onChange={{fn (mut @showGlobalFilter)}}>\n    Show global filter (<code>showGlobalFilter</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@useNumericPagination}}\n    @onChange={{fn (mut @useNumericPagination)}}>\n    Use numeric pagination (<code>useNumericPagination</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@showPageSize}}\n    @onChange={{fn (mut @showPageSize)}}>\n    \"Show page size select-box (<code>showPageSize</code>)\"\n  </PaperCheckbox>\n</p>\n<p>Used only for first table:</p>\n<p>\n  <PaperCheckbox\n    @value={{@filteringIgnoreCase}}\n    @onChange={{fn (mut @filteringIgnoreCase)}}>\n    \"Do filtering ignore case (<code>filteringIgnoreCase</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@multipleColumnsSorting}}\n    @onChange={{fn (mut @multipleColumnsSorting)}}>\n    \"Do sorting by multiple columns (<code>multipleColumnsSorting</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@doFilteringByHiddenColumns}}\n    @onChange={{fn (mut @doFilteringByHiddenColumns)}}>\n    \"Do filtering by hidden columns (<code>doFilteringByHiddenColumns</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@showCurrentPageNumberSelect}}\n    @onChange={{fn (mut @showCurrentPageNumberSelect)}}>\n    \"Show dropdown for current page number (<code>showCurrentPageNumberSelect</code>)\"\n  </PaperCheckbox>\n</p>\n<p>\n  <PaperCheckbox\n    @value={{@collapseNumPaginationForPagesCount}}\n    @onChange={{fn (mut @collapseNumPaginationForPagesCount)}}>\n    \"Collapse numeric pagination if pages count is greater (<code>collapseNumPaginationForPagesCount</code>)\"\n  </PaperCheckbox>\n</p>\n{{yield}}', { moduleName: 'dummy/templates/components/common-table-settings-paper.hbs' });
