import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>Infinity Scroll</h4>\n<pre>ember install ember-infinity</pre>\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"infinity-scroll.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"infinity-scroll.js\"/>\n  </div>\n</div>\n{{! BEGIN-SNIPPET infinity-scroll }}\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @useFilteringByColumns={{false}}\n  @themeInstance={{this.fw.themeInstance}}\n  as |MT|>\n  <MT.Table/>\n  <InfinityLoader @infinityModel={{this.model}} />\n</ModelsTableServerPaginated>\n{{! END-SNIPPET }}\n{{outlet}}', { moduleName: 'dummy/templates/examples/infinity-scroll.hbs' });
