import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h3>Examples</h3>\n{{#if this.fw.isBs4}}\n  <ExamplesNavBs4 @routes={{this.routes}} />\n  <br>\n  <ExamplesNavBs4 @routes={{this.routesWithAddons}} />\n  <br>\n  {{outlet}}\n{{/if}}\n{{#if this.fw.isBs5}}\n  <ExamplesNavBs5 @routes={{this.routes}} />\n  <br>\n  <ExamplesNavBs5 @routes={{this.routesWithAddons}} />\n  <br>\n  {{outlet}}\n{{/if}}\n{{#if this.fw.isPlainHtml}}\n  <ExamplesNavPlainHtml @routes={{this.routes}} />\n  <br>\n  <ExamplesNavPlainHtml @routes={{this.routesWithAddons}} />\n  <br>\n  {{outlet}}\n{{/if}}\n{{#if this.fw.isPaper}}\n  <ExamplesNavPaper @routes={{this.routes}} />\n  <br>\n  <ExamplesNavPaper @routes={{this.routesWithAddons}} />\n  <br>\n  <this.PaperCard as |card|>\n    <card.content>\n      {{outlet}}\n    </card.content>\n  </this.PaperCard>\n{{/if}}\n{{#if this.fw.isDefault}}\n  {{outlet}}\n{{/if}}', { moduleName: 'dummy/templates/examples.hbs' });
